.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

@font-face {
  font-family: "Russia";
  src: url("../fonts/Russia.woff2") format("woff2"), url("../fonts/Russia.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

body {
  min-width: 320px;
  font-size: 16px;
  min-height: 100vh;
  font-family: "Russia", "Arial", sans-serif;
  background-color: #3464e0;
  background-image: linear-gradient(84.71deg, #3464e0 1.19%, #2955c9 99.81%);
  color: #444444;
}

.page-header {
  max-width: 1333px;
  display: flex;
  justify-content: space-between;
  min-height: 72px;
  padding: 14px 10px;
  box-sizing: border-box;
  margin: 0 auto;
  position: relative;
}

.page-header__logo {
  font-size: 24px;
  line-height: 28px;
  color: #ffffff;
  text-decoration: none;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  margin-right: 20px;
}

.page-header__logo-image {
  width: 44px;
  height: 44px;
  margin-right: 14px;
}

@media (max-width: 1023px) {
  .page-header__logo {
    font-size: 22px;
    line-height: 26px;
  }
  .page-header__logo-image {
    width: 42px;
    height: 42px;
    margin-right: 5px;
  }
}

@media (max-width: 767px) {
  .page-header {
    flex-wrap: wrap;
  }
  .page-header__logo {
    margin-bottom: 15px;
  }
  .page-header__logo-image {
    margin-right: 10px;
  }
}

.site-list {
  flex-basis: 660px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-right: 20px;
  box-sizing: border-box;
}

.site-list__item a {
  font-size: 18px;
  line-height: 21px;
  color: #9cbaf0;
  text-decoration: none;
  transition: color 0.15s ease-in-out;
}

.site-list__item a:hover {
  color: #ffffff;
}

.site-list__item--active a {
  color: #ffffff;
}

@media (max-width: 1023px) {
  .site-list {
    flex-basis: 560px;
    margin-right: 15px;
  }
  .site-list__item a {
    font-size: 16px;
  }
}

@media (max-width: 767px) {
  .site-list {
    flex-basis: 100%;
    margin-right: 15px;
    flex-direction: column;
  }
  .site-list__item {
    margin-bottom: 5px;
  }
}

.user-list__login {
  display: flex;
  width: 175px;
  height: 42px;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.15s ease-in-out;
}

.user-list__login:hover {
  background-color: #1740aa;
}

.user-list__login-image {
  width: 26px;
  height: 25px;
  margin-right: 9px;
}

@media (max-width: 1023px) {
  .user-list__login {
    font-size: 0;
    width: auto;
    border-radius: 10px;
    padding-left: 10px;
    padding-right: 5px;
  }
  .user-list__login-image {
    margin-right: 0;
  }
}

@media (max-width: 767px) {
  .user-list__login {
    font-size: 14px;
    padding: 0;
    width: 100%;
    justify-content: flex-start;
    opacity: 0.6;
    transition: opacity 0.15s ease-in-out;
  }
  .user-list__login:hover {
    background-color: transparent;
    text-decoration: underline;
    opacity: 1;
  }
  .user-list__login-image {
    margin-right: 9px;
  }
}

.main-nav {
  display: flex;
  flex-basis: 72.4%;
  justify-content: space-between;
  align-items: center;
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__list:first-of-type {
  margin-right: 15px;
}

.main-nav__toggle {
  display: none;
}

@media (max-width: 1023px) {
  .main-nav {
    flex-basis: 70%;
    justify-content: flex-end;
  }
}

@media (max-width: 767px) {
  .main-nav {
    flex-basis: 100%;
    flex-wrap: wrap;
    padding-left: 53px;
    padding-right: 53px;
    justify-content: flex-start;
  }
  .main-nav__list:first-of-type {
    margin-right: 0;
  }
  .main-nav__toggle {
    display: block;
    position: absolute;
    top: 14px;
    right: 0;
    z-index: 2;
    width: 70px;
    height: 44px;
    background-color: transparent;
    cursor: pointer;
    border: none;
    transition: background-color 0.15s ease-in-out;
  }
  .main-nav__toggle:hover {
    background-color: #1740aa;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 14px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: none;
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
}

.page-main {
  position: relative;
}

.modal {
  margin: 0 auto;
  justify-content: center;
  padding-top: 50px;
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
  margin-bottom: 20px;
}

.modal__open {
  display: flex;
}

.tabs {
  position: relative;
  background: #ffffff;
  border-radius: 4px;
  padding-top: 35px;
  transition: width 0.15s ease-in-out;
}

.tabs::before {
  content: "";
  position: absolute;
  width: calc(100% - 30px);
  left: 15px;
  border-radius: 4px;
  height: 100%;
  top: -21px;
  z-index: -1;
  background-color: rgba(255, 255, 255, 0.8);
}

.tabs::after {
  content: "";
  position: absolute;
  width: calc(100% - 60px);
  left: 30px;
  border-radius: 4px;
  height: 100%;
  top: -38px;
  z-index: -1;
  background-color: rgba(255, 255, 255, 0.6);
}

#login:checked ~ .tabs__list .tabs__item:first-of-type,
#register:checked ~ .tabs__list .tabs__item:last-of-type {
  display: block;
}

.tabs__form-login input {
  width: 338px;
  padding-left: 16px;
}

.tabs__form-login .form__group {
  margin-bottom: 14px;
}

.tabs__form-login .form__group:last-of-type {
  margin-bottom: 0;
  margin-top: 42px;
}

.tabs__form-login .form__group:last-of-type a {
  color: #3464e0;
  text-decoration: none;
}

.tabs__form-login .form__group:last-of-type a:hover {
  text-decoration: underline;
}

#login:checked ~ .tabs__control label:first-of-type,
#register:checked ~ .tabs__control label:last-of-type {
  color: #3464e0;
}

.tabs__item {
  display: none;
}

.tabs__item:first-of-type {
  padding-bottom: 50px;
  padding-left: 76px;
  padding-right: 76px;
}

.tabs__item:last-of-type {
  padding-bottom: 34px;
  padding-left: 33px;
  padding-right: 16px;
}

.tabs__control {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

.tabs__control label {
  word-break: break-word;
  font-size: 26px;
  line-height: 26px;
  color: #c3cad6;
  cursor: pointer;
  transition: color 0.15s ease-in-out;
}

.tabs__control label:first-of-type {
  margin-right: 20px;
}

.tabs__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (max-width: 767px) {
  .tabs .tabs__item {
    width: 310px;
    box-sizing: border-box;
    padding-left: 10px;
    padding-right: 10px;
  }
  .tabs .tabs__item input {
    width: 100%;
  }
  .tabs__control label {
    font-size: 22px;
  }
  .tabs__control label:first-of-type {
    margin-right: 5px;
  }
}

.form input::placeholder {
  font-size: 14px;
  color: #c3cad6;
}

.form label {
  line-height: 26px;
  margin-bottom: 5px;
  cursor: pointer;
}

.form label span {
  color: #e06364;
}

.form button[type="submit"] {
  padding: 9px 17px;
  background-color: #3464e0;
  border: none;
  box-sizing: border-box;
  border-radius: 4px;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.15s ease-in-out;
}

.form button[type="submit"]:hover {
  background-color: #4065c6;
}

.form button[type="submit"]:active {
  background-color: #3b58a1;
}

.form button[type="button"] {
  padding: 9px 5px;
  width: 136px;
  border: 1px solid #c3cad6;
  box-sizing: border-box;
  border-radius: 4px;
  color: #c3cad6;
  background-color: transparent;
  cursor: pointer;
  transition: color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}

.form button[type="button"]:hover {
  border-color: #3464e0;
  color: #3464e0;
}

.form button[type="button"]:active {
  color: #ffffff;
  background-color: #3464e0;
}

.form .form__group-bt {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.form__group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.form__group input:not([type="checkbox"]) {
  border: 1px solid #f1f4f9;
  box-sizing: border-box;
  border-radius: 4px;
  height: 44px;
}

.form__wrapper {
  width: 841px;
  padding-right: 250px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  box-sizing: border-box;
}

.form__wrapper .form__group {
  margin-bottom: 22px;
}

.form__wrapper .form__group:first-of-type {
  width: 100%;
}

.form__wrapper input {
  width: 280px;
  padding-left: 12px;
}

.form__wrapper + .form__group {
  margin-bottom: 24px;
}

.form__group-item {
  margin-bottom: 13px;
  padding-left: 36px;
  box-sizing: border-box;
  position: relative;
}

.form__group-item label::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border: 1px solid #3464e0;
  box-sizing: border-box;
  border-radius: 2px;
  left: 0;
  top: 2px;
}

.form__group-item:last-of-type {
  margin-bottom: 0;
}

.form__group-item input[name="personal-data"]:checked + label::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #3464e0;
  left: 5px;
  top: 7px;
}

.form__group-item input[name="notification"]:checked + label::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #3464e0;
  left: 5px;
  top: 6.7px;
}

@media (max-width: 1023px) {
  .form__wrapper {
    width: 640px;
  }
}

@media (max-width: 767px) {
  .form__wrapper {
    width: 100%;
    padding-right: 0;
  }
  .form__wrapper .form__group {
    width: 100%;
  }
  .form > .form__group:last-of-type {
    flex-direction: column;
  }
  .form > .form__group:last-of-type button {
    width: 100%;
  }
  .form > .form__group:last-of-type button:first-of-type {
    margin-bottom: 10px;
  }
}
